.bingmain{
    margin: 2.5rem;
}
.bing-container-row{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    flex-direction: row;
}

.bing-community-container{
    display: flex;
    flex-direction: column;
    max-width: 30%;
    justify-content: left;
    align-items:flex-start;
    text-align: justify;
    /* text-align: left; */
}

.bing-community-container > h2{
    font-family: "Josefin Sans";
    font-size: 2rem;
    font-weight: 700;
    line-height: 2rem;
    letter-spacing: 2.1875px;
    margin: 1.3rem 0 1.5rem;
}

.bing-para{
    font-family: 'Lato';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 49px;
    letter-spacing: 1px;
    color: #000000;
    /* text-align: left; */
}

.community-para{
    font-family: 'Lato';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 28px;
    letter-spacing: 1px;
    color: #171A1C;
}

.bing-line{
    width: 1px;
    height: 320px;
    margin-left: 4.6rem;
    background: #4D545C;
}

@media screen and (min-width:768px) and (max-width: 1194px){
   .bing-line{
        margin: 3rem 0;
        height: 1px;
        width: 100%;
    }
    .bing-container-row{
       flex-direction: column;
    }
    .bing-community-container{
        max-width: 70%;
        text-align: left;
        margin-left: 3rem;
    }
}

@media screen and (max-width: 767px) {
    .bing-container-row{
        flex-direction: column;
    }
    .bing-line{
        margin: 3rem 0;
        height: 1px;
        width: 100%;
    }
    .bing-community-container{
        max-width: 90%;
        text-align: left;
    }
}

