.PhaseGoalCard {
    margin: 10% 20% 10% ;
  }

.PhaseGoal-container{
    position: relative;
    /* margin-left: 18%;
    margin-right: 18%;
    margin-top: 25px;
    margin-bottom: 60px; */
    background: #FFFFFF;
    box-shadow: 0px 0px 18px rgba(0, 0, 0, 0.25);
}

.PhaseGoal-row{
    /* margin-right: -15px;
    margin-bottom: -15px;
    margin-left: -15px; */
    align-content: stretch;
    align-items: center;
}

.PhaseGoal-col{
    margin-bottom: 15px;
    padding-right: 15px;
    padding-left: 15px;
}

.PhaseGoal-text-wrapper{
    text-align: center;
    padding: 5% 20% 5%;
}

.heading-1{
    margin: 10px auto;
    font-family: 'Josefin Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 30px;
    line-height: 30px;
    letter-spacing: 1.875px;
    color: #171A1C;
}

.heading-2{
    margin: 10px auto;
    font-family: 'Lato';
    font-style: normal;
    font-weight: 700;
    font-size: 45px;
    line-height: 54px;
    text-align: center;
    letter-spacing: 2.8125px;
    color: #171A1C;
}

.PhaseGoal-subtitle{
    margin: 10px auto;
    font-family: 'Lato';
    font-style: normal;
    font-weight: 800;
    font-size: 16px;
    line-height: 19px;
    text-align: center;
    letter-spacing: 0.875px;

    color: #171A1C;
}

.PhaseGoal-text-1{
    max-width: 431px;
    margin: 10px auto;
    font-family: 'Lato';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    text-align: center;
    letter-spacing: 1px;

    color: #171A1C;
}

.PhaseGoal-text-2{
    max-width: 510px;
    margin: 10px auto;
    font-family: 'Lato';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 22px;
    letter-spacing: 0.875px;
    color: #000000;
}

.PhaseGoal-img-wrapper{
    max-width: 100%;
}

.PhaseGoal-img{
    max-width: 97px;
    margin: 0 auto;
}

.ipad-view{
    display: none;
}

@media screen and (min-width:768px) and (max-width:1194px) {
   .desktop-view{
        display: none;
   } 
   .desktop-view{
        display: none;
   }
   .PhaseGoal-text-wrapper{
        padding: 2rem 3rem 2rem;
        text-align: center;
   }
   .ipad-view{
        display: flex;
        flex-direction: column;
   }

   .ipad-view >h1, .mobile-view{
        font-family: 'Josefin Sans';
        font-style: normal;
        font-weight: 700;
        font-size: 1.5rem;
        line-height: 1.5rem;
        text-align: center;
        letter-spacing: 1.875px;
        color: #171A1C;
        /* max-width: 3rem; */
   }

   .mobile-view{
        margin: 4px;
   }

   .ipad-view >p{
        font-family: 'Lato';
        font-style: normal;
        font-weight: 600;
        font-size: 0.7rem;
        line-height: 0.7rem;
        text-align: center;
        letter-spacing: 0.875px;
        color: #171A1C;
        max-width: 25px;
   }

   .phase-row{
        display: flex;
        flex-wrap: wrap;
        flex-direction: row;
        justify-content: space-between;
        margin: 2rem 0.5rem;
   }

   .phase-col{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 0.4rem;
        max-width: 32%;
   }

   .phase-col >h1{
        font-family: 'Lato';
        font-style: normal;
        font-weight: 700;
        font-size: 1.4rem;
        line-height: 1.6rem;
        text-align: center;
        letter-spacing: 2.8125px;
        color: #171A1C;
   }

   .phase-col > img{
        max-width: 2rem;
        max-height: 2rem;
        margin: 4px auto;
   }

}

@media screen and (max-width:767px) {
    .PhaseGoal-text-1, .desktop-view{
        display: none;
    }

    .PhaseGoalCard{
        margin: 3rem;
    }
    
    .ipad-view{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin: 2rem;
    }

    .ipad-view >h1{
        font-family: 'Josefin Sans';
        font-style: normal;
        font-weight: 700;
        font-size: 1.5rem;
        line-height: 1.5rem;
        letter-spacing: 1.875px;
        color: #171A1C;
        margin: 1rem;
    }

    .PhaseGoal-text-wrapper{
        padding: 1rem  4px;
        margin: 2rem 0.5rem;
    }

    .phase-col{
        margin: 2rem;
    }

    .mobile-view{
        display: none;
    }
}