.nft-main{
    margin: 4rem 2.5rem;
}

.nft-row{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    flex-direction: row;
}

.nft-col{
   text-align: left; 
   max-width: 40%;
   display: flex;
   flex-direction: column;
}

.nft-border{
    /* width: 463px; */
    width: 30em;
    /* height: 581px; */
    border: 2px solid #4D545C;
    border-radius: 4px;
    padding: 1rem 1.5rem;
}

.nft-border >h1{
    font-family: 'Josefin Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 2.1rem;
    line-height: 2rem;
    /* text-align: center; */
    letter-spacing: 2.1875px;
    color: #171A1C;
}

.nft-border>img{
    display: block;
    margin-left: auto;
    margin-right: auto;
    /* width: 55%; */
    width: 263px;
    height: 369px;
}

.nft-para-row{
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.nft-para-col{
    display: flex;
    flex-direction: column;
    max-width: 45%;
}


.nft-button{
    /* background-color: ; */
    text-transform: uppercase;
    background: #417B66;
    border: white;
    border-radius: 6px;
    font-family: 'Josefin Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 1.5rem;
    line-height: 1.5rem;
    letter-spacing: 1.25px;
    color: #FFFFFF;
    padding: 0.6rem 1rem 0.6rem;
    /* margin-right: 0.5rem ; */
}

.nft-button:hover{
    background:#4D545C;
}

.nft-col >h1 {
    font-family: 'Josefin Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 50px;
    line-height: 50px;
    letter-spacing: 3.125px;
    text-align: left;
    color: #171A1C;
    /* max-width: 85%; */
}

.nft-col >p{
    font-family: 'Lato';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 28px;
    letter-spacing: 1px;
    color: #171A1C;
    margin: 1rem 0;
}

.box{
    box-sizing: border-box;
    border: 1px solid #4D545C;
    border-radius: 5px;
    max-width: fit-content;
    padding: 10px 20px;
}

.box >h3{
    font-family: 'Lato';
    font-style: normal;
    font-weight: 800;
    font-size: 16px;
    line-height: 35px;
    letter-spacing: 1px;
    text-decoration-line: underline;
    text-transform: uppercase;
    color: #171A1C;
}

.series{
    font-family: 'Lato';
    font-style: normal;
    font-weight: 400;
    font-size: 1.2rem;
    line-height: 2rem;
    letter-spacing: 1.375px;
    color: #8895A4;
    margin: 0 0 0.7rem;
}

.box >p {
    color: #171A1C;
    font-family: 'Lato';
    font-style: normal;
    font-weight: 400;
    line-height: 35px;
    letter-spacing: 1.375px;
}

.nft-eth{
    font-family: 'Lato';
    font-style: normal;
    font-weight: 600;
    font-size: 3rem;
    line-height: 2.2rem;
    letter-spacing: 3.3125px;
    color: #2E5DEF;
}

.nft-button-mobile{
    display: none;
}

.nft-col >p >span{
    font-family: 'Lato';
    font-style:normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 28px;
    letter-spacing: 1px;
    color: #2E5DEF;
    text-transform: uppercase;
    margin: 4px;
}

@media screen and (min-width:768px) and (max-width: 1194px) {
    .nft-row{
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .nft-col{
        display: flex;
        max-width: 90%;
        margin: 0.5rem 0 1rem;
        align-items: center;
    }
    .nft-button-mobile{
        display: none;
    }
}

@media screen and (max-width:767px) {
    .nft-row{
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .nft-col{
        display: flex;
        max-width: 80%;
        margin: 0.5rem 0 1rem;
        align-items: center;
    }
    .nft-col >h1{
        font-size: 30px;
        line-height: 30px;
    }

    .nft-col >p{
        font-size: 14px;
        line-height: 25px;
    }

    .nft-border{
        width: 329px;
        height: 467px;
    }
    .nft-border >h1{
        font-size: 30px;
        line-height: 30px;
    }
    .nft-border >img{
        width: 213px;
        height: 299px;
    }
    .series{
        font-size: 18px;
        line-height: 35px;
    }
    .nft-eth{
        font-size: 45px;
        line-height: 35px;
    }
    .nft-button{
        display: none;
    }
    .nft-button-mobile{
        display: block;
        text-transform: uppercase;
        background: #417B66;
        border: white;
        border-radius: 6px;
        font-family: 'Josefin Sans';
        font-style: normal;
        font-weight: 700;
        font-size: 1.5rem;
        line-height: 1.5rem;
        letter-spacing: 1.25px;
        color: #FFFFFF;
        padding: 0.6rem 1rem 0.6rem;
        margin: 1.5rem;
    }
}