.creator-container{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
}

.creator-row{
    display: flex;
    flex-direction: row;
    max-width: 90%;
    align-items: center;
    justify-content: center;
    margin: 0 2rem 0 2rem;
    padding: 0 2rem 0 2rem;
}

.creator-col{
    max-width: 55%;
    display: flex;
    flex-direction: column;
    padding: 3rem 2rem;
    /* justify-content: space-between; */
    align-items: flex-start;
}

.creator-img-container{
    display: flex;
    flex-direction: column;
    max-width: 35%;
}
.creator-img{
   /* display: block;  */
   max-width: 23rem;
   max-height: 24rem;
}
.creator-heading, .title-span{
    font-family: 'Josefin Sans';
    font-style: normal;
    font-weight: 400;
    font-size: 1.8rem;
    line-height: 1.9rem;
    letter-spacing: 2.1875px;
    color: #171A1C;  
    max-width: 84%;
    text-align: start;
    margin: 0 0 0 2rem;
}

.creator-col >p{
    font-family: 'Lato';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 35px;
    letter-spacing: 1.125px;
    padding: 0 0.3rem 0 0.3rem;
    /* margin: 0.6rem; */
    width: 90%;    
}

.creator-quote{
    margin: 1rem 2rem 0 2rem;
    text-align: justify;
}
.quote-title{
    text-align: end;
} 

.creator-img-mobile{
    display: none;
}

.title-name{
    color:#2E5DEF;
    margin: 4px;
}

.title-span{
    color: #417B66;
    margin: 4px;
    text-decoration: underline;
}

@media screen and (min-width: 768px) and (max-width: 1194px) {
    .creator-col{
        padding: 1.5rem 1rem;
    }
    .creator-heading ,.title-span{
        max-width: 95%;
        font-size: 1.1rem;
        line-height: 1.2rem;
        margin: 0 0 0 1rem;
    }
    .title-span{
        margin: 3px;
    }

    .creator-col >p{
        font-size: 0.8rem;
        line-height: 1rem;
        width: 70%;
    }
    .creator-img{
        max-width: 20rem;
        max-height: 18rem;
    }
}

@media screen and (max-width: 767px) {

    .creator-row{
        flex-direction: column;
        margin: 0.4rem 1rem;
        padding: 2rem 1rem;
    }

    .creator-img-container{
        display: none;
    }
    .creator-col{
        max-width: 95%;
        margin: 0.3rem;
    }

    .creator-heading, .title-span{
        max-width: 100%;
        font-size: 1.4rem;
        line-height: 2rem;
        margin: 1rem;
    }

    .creator-img-mobile{
        display: block;
    }

     .creator-img{
        max-width: 20rem;
        max-height: 18rem;
    }
}