.collect-container{
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    margin: 1rem;
    max-width: 95%;
    align-items: center;
    justify-content: center;
}

.collect-container >h1{
    font-family: 'Josefin Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 2.5rem;
    line-height: 2.5rem;
    text-align: center;
    letter-spacing: 3.125px;
    color: #171A1C;
    margin: 1.2rem;
}

.collect-container > p{
    font-family: 'Lato';
    font-style: normal;
    font-weight: 400;
    font-size: 1.2rem;
    line-height: 2.1rem;
    text-align: center;
    letter-spacing: 1.375px;
    color: #171A1C;
    margin: 1.5rem;
}

.token-row{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    margin: 1.5rem;
}

.card-container{
    width: 20rem;
    background: #FFFFFF;
    border: 2px solid  #4D545C;
    border-radius: 6px;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 20rem;
    margin: 0 1rem 0;
}

.card-container > h1{
    font-family: 'Josefin Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 1.9rem;
    line-height: 2rem;
    text-align: center;
    letter-spacing: 1.875px;
    color: #171A1C;
    margin: 0.3rem 0 1rem;
}

.card-container > p{
    font-family: 'Lato';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 22px;
    letter-spacing: 0.875px;
    color: #171A1C;
    text-align: left;
}

.card-img{
    background: #417B66;
    border: 4px double white;
    border-radius: 50%;
    height: 5rem;
    width: 5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0.5rem;
}
.card-img{
    max-width: 100%;
    display: block;
}

@media screen and (min-width:768px) and (max-width: 1194px){
    .card-container{
        max-width: 13rem;
        max-height: 20rem;
    }
    .collect-container >h1{
        font-size: 2rem;
    }
    .creator-row{
        margin: 0;
    }
    .collect-container >p{
        font-size: 1.3rem;
    }

    .card-container{
        min-width: 10rem;
        min-height: 20rem;
    }

    .card-container >h1{
        font-size: 20px;
        line-height: 20px;
        letter-spacing: 1.875px;
    }

    .card-container >p{
        font-size: 14px;
        line-height: 22px;
        letter-spacing: 0.875px;
    }

    .card-img{
        width: 82px;
        height: 82px;
    }

}

@media screen and (max-width: 767px) {
    .token-row{
        flex-direction: column;
    }

    .card-container >h1{
        font-size: 20px;
        line-height: 20px;
        letter-spacing: 1.875px;
    }
    .card-container{
        max-width: fit-content;
        min-width: 15rem;
        max-height: 20rem;
        margin: 1rem;
    }
}
